@charset "utf-8";

/* 메인서식 */
h2{
  text-align: center;
}

/* 입력폼 서식 */
.inputData {
  max-width: 880px;
  margin: 0 auto;
  padding: 20px;
  box-sizing: border-box;
  background: #009C75;
}
/* .inputData label{
  display: inline-block;
  width: 120px;
  text-align:justify;
}
.inputData label:after{
	content: '';
  display: inline-block;
  width: 100%;
} */
.inputData label{
  display:none;
}
.inputData input{
  width: 100%;
  height: 36px;
  box-sizing: border-box;
  background: #fff;
  border:none;
  border-radius:3px;
}
.inputData input[type="button"]{
  background: #bd0000;
  color:#fff;
}

/* 리스트 서식 */
.tourList {
  display: flex;
  flex-wrap: wrap;
  max-width: 880px;
  margin: 0 auto;
  padding: 0;
  border: 1px solid #009C75;
  box-sizing: border-box;
}
.tourList li {
  list-style: none;
  width: 50%;
  padding: 20px;
  box-sizing: border-box;
  border: 1px solid #009C75;
}
.tourList li img {
  width: 100%;
  max-width: 410px;
}
.tourList li .productName{
  display:block;
  width: 100%;
  max-width: 410px;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  font-weight: bold;
  font-size: 1.2rem;
  height: 3rem;
}
.tourList li .delBtn{
  text-align: right;
}
.tourList li input[type="button"]{
  background:#bd0000;
  border: none;
  color:#fff;
  padding: 10px 20px;
  border-radius: 3px;
}
